<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" label-width="110px">
            <el-row class="toolbar bgc-fff">
                <el-col :span="20">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">{{form.work_status_name}}({{form.stage_name}})</span></h2>
                    <p class="fs-14">当前处理人：{{form.operater}}</p>
                </el-col>
                <!-- <el-col :span="12" class="ta-r">
                    <el-button type="primary" icon="plus">导出</el-button>
                    <el-button type="primary" icon="plus">打印</el-button>
                </el-col> -->
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车牌号">
                                {{form.basic_info.plate_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="车主姓名">
                                {{form.basic_info.band_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="vin号">
                                {{form.basic_info.vin_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="移动电话">
                                {{form.basic_info.mobile}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车辆品牌">
                                {{form.basic_info.car_brand}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="挂靠公司">
                                {{form.basic_info.bus_mech_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="办理项目">
                        <el-checkbox-group v-model="form.item_type" :disabled="isDisabledTransactor()">
                            <el-checkbox label="1">车牌补办</el-checkbox>
                            <el-checkbox label="2">行驶证补办</el-checkbox>
                            <el-checkbox label="3">营运证补办</el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公告型号">
                                {{form.basic_info.foreign_model}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="马力">
                                {{form.basic_info.horse_power}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="驱动形式">
                                {{form.basic_info.drive_form}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="颜色">
                                {{form.basic_info.color}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="办理人" v-if="isShowTransactor()">
                                <el-radio-group v-model="form.tran_type" :disabled="isDisabledTransactor()">
                                    <el-radio label="1">公司外勤</el-radio>
                                    <el-radio label="2">专业人士</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <!-- <el-col :span="12">
                            <el-form-item label="公司人员">
                                {{form.tran_name}}
                            </el-form-item>
                        </el-col> -->
                    </el-row>

                    <el-row v-if="isShowTransactor()">
                        <el-col :span="12">
                            
                            <el-form-item label="办理人移动电话">
                                <template v-if="form.tran_type==1">{{form.telphone}}</template>
                                <el-input v-model.trim="form.telphone" placeholder="请输入办理人移动电话" :disabled="isDisabledTransactor()" v-else></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="指定外勤办理" v-if="form.tran_type==1">
                                <el-select v-model="form.tran_id" placeholder="请选择" class="mr10" @change="getTelphone" :disabled="isDisabledTransactor()">
                                    <el-option
                                        v-for="item in optionsUserList"
                                        :key="item.id"
                                        :label="item.user_real_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="办理人姓名" v-else>
                                <el-input v-model.trim="form.tran_name" placeholder="请输入办理人姓名" :disabled="isDisabledTransactor()"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

                <el-collapse-item title="　办理信息" name="2" v-if="isShowAdvance()">

                    <el-form-item label="补办">
                        <el-upload
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_sup_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.sup_img" :src="form.sup_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>

                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　垫款信息" name="3" v-if="isShowAdvance()">

                    <el-form-item label="垫款金额">
                        <el-input v-model="form.spot_money"></el-input>
                    </el-form-item>

                    <el-form-item label="费用凭证">
                        <el-upload
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_spot_money_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.spot_money_img" :src="form.spot_money_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="备注">
                        <el-input v-model="form.spot_remark"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　审核资料" name="4">

                    <el-form-item label="资料名称">
                        <el-input v-model="form.splicense_name" :disabled="isDisabledReceipt()"></el-input>
                    </el-form-item>

                    <el-form-item label="资料附件">
                        <el-upload
                            :disabled="isDisabledReceipt()"
                            :action="IMG_UPLOAD_URL"
                            :on-success="handleUploadSuccess_splicense_img"
                            :file-list="form.splicense_img"
                            list-type="picture-card">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　付款信息" name="5" v-if="isShowPayment()">

                    <el-form-item label="付款金额">
                        <el-input v-model="form.pay_info.money" :disabled="isDisabledPayment()"></el-input>
                    </el-form-item>

                    <el-form-item label="付款银行">
                        <el-input v-model="form.pay_info.pay_bank" :disabled="isDisabledPayment()"></el-input>
                    </el-form-item>

                    <el-form-item label="付款银行账户">
                        <el-input v-model="form.pay_info.pay_bank_account" :disabled="isDisabledPayment()"></el-input>
                    </el-form-item>

                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.pay_info.pay_remark" :disabled="isDisabledPayment()"></el-input>
                        <p>注：请填写支付专业人士办理业务的费用</p>
                    </el-form-item>

                    <el-form-item label="出纳打款凭证" v-if="isShowVoucher()">
                        <el-upload
                            :disabled="isDisabledVoucher()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_pay_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.pay_info.pay_img" :src="form.pay_info.pay_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    
                    <el-form-item label="备注" v-if="isShowVoucher()">
                        <el-input type="textarea" v-model="form.pay_info.pay_img_remark" :disabled="isDisabledVoucher()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　收款信息" name="6">

                    <el-form-item label="收款信息选择">
                        <el-radio-group v-model="form.some_collect_cast.pay_type" :disabled="isDisabledReceipt()">
                            <el-radio label="1">立即支付</el-radio>
                            <el-radio label="2">延迟付款</el-radio>
                            <el-radio label="3">已支付</el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="费用金额" v-if="form.some_collect_cast.pay_type == 2">
                        <el-input v-model="form.some_collect_cast.money" :disabled="isDisabledReceipt()"></el-input>
                    </el-form-item>

                    <el-row v-if="form.some_collect_cast.pay_type == 1">
                        
                        <el-form-item label="车主支付方式">
                            <el-radio-group v-model="form.some_collect_cast.pay_method" :disabled="isDisabledReceipt()">
                                <el-radio label="1">现付</el-radio>
                                <el-radio label="2">转账</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="本次费用">
                            <el-input v-model="form.some_collect_cast.this_cost" @change="handelOutstanding" :disabled="isDisabledReceipt()"></el-input>
                        </el-form-item>

                        <el-form-item label="未结清费用">
                            <el-checkbox-group v-model="form.some_collect_cast.choose" @change="handelOutstanding" :disabled="isDisabledReceipt()">
                                <el-checkbox v-for="item in form.no_pay" :label="item.id" :key="item.id">{{item.item_name}} ￥{{item.money}}</el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>

                        <el-form-item label="费用凭证">
                            <el-upload
                                :disabled="isDisabledReceipt()"
                                class="avatar-uploader"
                                :action="IMG_UPLOAD_URL"
                                :show-file-list="false"
                                :on-success="handleUploadSuccess_cost_img"
                                :before-upload="handleBeforeUpload">
                                <img v-if="form.some_collect_cast.cost_img" :src="form.some_collect_cast.cost_img" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>

                        <el-form-item label="支付总费用">
                             {{form.some_collect_cast.together_cost}}
                        </el-form-item>

                    </el-row>

                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.some_collect_cast.refund_remark" :disabled="isDisabledReceipt()"></el-input>
                    </el-form-item>

                    <el-form-item label="选择审核人" class="mt15" v-if="isShowFirstAuditor()">
                        <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="7" v-if="isShowAudit()">
                    
                    <el-collapse v-model="collapseAuditActive">

                        <el-collapse-item title="　收款审核" name="1" v-if="form.exec_status.stage_id!=0 && form.exec_status.node!=110">
                            <el-card class="box-card mb15" v-for="(item,index) in form.handle_record.refund" :key="index">
                                <div><span class="display-ib ta-r w70">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w70">审核意见：</span>{{item.exec_op_type==1?'通过':'驳回'}}</div>
                                <div><span class="display-ib ta-r w70">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w70">审核人ID：</span>{{item.exec_user_name}}</div>
                                <div><span class="display-ib ta-r w70">审核时间：</span>{{item.create_time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <el-collapse-item title="　付款审核" name="2" v-if="form.exec_status.stage_id==8 ||form.exec_status.stage_id==15">
                            <el-card class="box-card mb15" v-for="(item,index) in form.handle_record.pay" :key="index">
                                <div><span class="display-ib ta-r w70">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w70">审核意见：</span>{{item.exec_op_type==1?'通过':'驳回'}}</div>
                                <div><span class="display-ib ta-r w70">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w70">审核人ID：</span>{{item.exec_user_name}}</div>
                                <div><span class="display-ib ta-r w70">审核时间：</span>{{item.create_time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <template v-if="isShowOpinion()">
                        <el-form-item label="审核意见" class="mt15">
                            <el-select v-model="form.opinion" placeholder="请选择" class="mr10">
                                <el-option label="通过" value="1"></el-option>
                                <el-option label="驳回" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="备注">
                            <el-input type="textarea" v-model="form.remark"></el-input>
                        </el-form-item>
                        <el-form-item label="选择审核人" class="mt15" v-if="form.opinion==1">
                            <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                <el-option
                                    v-for="item in optionsCompany"
                                    :key="item.id"
                                    :label="item.mc_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                            <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                <el-option
                                    v-for="item in optionsExecUser"
                                    :key="item.id"
                                    :label="item.user_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </template>

                    <el-form-item label="选择审核人" class="mt15" v-if="isShowLastAudit()">
                        <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    </el-collapse>
                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit(1)" v-if="pageType != 'view'">提 交</el-button>
                <el-button type="primary" @click="handleSubmit(2)" v-if="isShowSave()">保 存</el-button>
                <el-button @click="$router.back()">返 回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/trafficManagement'
    import { organizationListUl } from '@/api/enterpriseManagement'
    import { requestUrl } from '@/api/config'
    export default {
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                loading: false,
                pageType: 'add',
                optionsExecUser:[],
                optionsCompany:[],
                optionsUserList:[],
                form: 
{
    cp_id:'',
    opinion: '1',
    tran_type: "1",
    tran_id: "",
    tran_name: "",
    telphone: "",
    after_user_id: "",
    remark: "",
    item_type: [],
    lean_img: [],
    safe_img: [],
    splicense_img: [],
    spot_money_img: '',
    spot_money: '',
    spot_remark: '',
    sup_img: '',
    work_id: "0",
    node_id: "0",
    some_collect_cast: {
        money: "0",
        item_id: "1",
        pay_type: "1",
        pay_method: "1",
        this_cost: "",
        together_cost: "",
        cost_img: "",
        remark: "",
        choose: [],
    },
    basic_info: {
        band_id: "2",
        band_name: "王五",
        bus_mech_name: "合肥传贤",
        car_brand: "宝马",
        car_class: "自动挡",
        car_series: "宝马飞扬系列",
        color: "红色",
        drive_form: "4*6",
        drive_license_expire: "2010-10-10",
        drive_license_number: "",
        drive_license_register: "2010-10-10",
        foreign_model: "Gh2230e903290e",
        fuel_type: "汽油",
        horse_power: "660",
        id: "2",
        id_card: "763727388293727",
        mobile: "17688909987",
        op_license_expire: "2010-10-10",
        op_license_number: "",
        op_license_register: "2010-10-10",
        plate_number: "甘M44509",
        vin_number: "QWERTASDFG12ZX345"
    },
    no_pay_money: [
        {
            id: "1",
            item_id: "0",
            item_name: "未付款项",
            money: "100"
        }
    ],
    pay_info: {
        money: '',
        pay_bank: '',
        pay_bank_account: '',
        pay_remark: '',
        pay_img: '',
        pay_img_remark: ''
    }
}
            }
        },
        mounted(){
            this.getFormData()
        },
        methods: {
            isDisabledReceipt () {
                return this.form.node_id!=110 && this.form.node_id!=119 && this.form.node_id!=0
            },
            isDisabledPayment(){
                return this.form.node_id>120 && this.form.node_id!=0 && this.form.node_id!=134
            },
            isDisabledTransactor(){
                return this.form.node_id>120
            },
            isDisabledVoucher(){
                return this.form.node_id > 127
            },
            isShowTransactor() {
                return this.form.node_id>=120
            },
            isShowVoucher(){
                return this.form.node_id>=127 && this.form.node_id!=134
            },
            isShowAdvance(){
                return this.form.node_id>=132 && this.form.node_id!=134
            },
            isShowPayment(){
                return this.form.node_id>=120 && this.form.tran_type==2
            },
            isShowAudit(){
                return this.form.node_id!=110 && this.form.node_id!=0
            },
            isShowSave(){
                if(this.pageType != 'view'){
                    return this.form.work_status!=2
                }
            },
            isShowOpinion(){
                return this.form.work_status==2 && this.form.node_id!=132
            },
            isShowLastAudit(){
                if(this.form.stage_id==8){
                    return this.form.work_status==1 || this.form.work_status==3
                }
            },
            isShowFirstAuditor(){
                return this.form.node_id==110|| this.form.node_id==0
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;

                _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                // 公司人员
                API.apiNewCarDoorGetPosPer({}).then(res => {
                    if(res.code == 0){
                        this.optionsUserList = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                this.loading=true
                API.apiLicencesReapplyView({id:_query.id}).then(res => {
                    this.loading=false
                    if(res.code == 0){
                        Object.assign(this.form, res.data)
                        this.getExecUser()
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getTelphone(id){
                API.apiNewCarDoorGetUserInfo({id: id}).then(res => {
                    if(res.code == 0){
                        this.form.telphone = res.data.telphone
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getExecUser(){
                // 审核人
                let type='',
                    get_type=''
                if(this.form.some_collect_cast.pay_type == 1){
                    this.form.some_collect_cast.pay_method==1?type=1:type=2
                }else{
                    type=3
                }
                if(this.form.stage_id==7 || this.form.stage_id==0){
                    if(this.form.work_status==1 || this.form.work_status==3){
                        get_type=1
                    }else{
                        get_type=2
                    }
                }else{
                    if(this.form.work_status==1 || this.form.work_status==3){
                        get_type=3
                    }else{
                        get_type=2
                    }
                }
                let params = {
                    cp_id:this.form.cp_id,
                    type:type,
                    get_type:get_type,
                    node_id:this.form.node_id,
                }
                this.form.after_user_id = ''
                API.apiLicencesReapplyGetExecUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleSubmit(submitType) {
                if(this.form.node_id==110 || this.form.node_id==119 || this.form.node_id==0){
                    let params = {
                        ...this.form.some_collect_cast,
                        splicense_img: this.form.splicense_img,
                        splicense_name: this.form.splicense_name,
                        item_type: this.form.item_type,
                        after_user_id: this.form.after_user_id,
                        submit_type: submitType,
                        bus_id: this.form.bus_id,
                        id: this.form.id
                    }
                    API.apiLicencesReapplySaveRefundInfo(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id==120 || this.form.node_id==134){
                    let params = {
                        tran_type: this.form.tran_type,
                        tran_id: this.form.tran_id,
                        tran_name: this.form.tran_name,
                        telphone: this.form.telphone,
                        ...this.form.pay_info,
                        after_user_id: this.form.after_user_id,
                        submit_type: submitType,
                        bus_id: this.form.bus_id,
                        id: this.form.id
                    }
                    API.apiLicencesReapplySavePayInfo(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else{
                    let payParams={
                        pay_id: this.form.pay_info.id,
                        pay_img: this.form.pay_info.pay_img,
                        pay_img_remark: this.form.pay_info.pay_img_remark,
                    }
                    let perfectParams = {
                        sup_img: this.form.sup_img,
                        perfect_info: 1
                    }
                    let params = {
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        remark: this.form.remark,
                        reject_type: this.form.stage_id==7?'1':'2',
                        exam_type: this.form.opinion,
                        tran_type: this.form.tran_type,
                        tran_name: this.form.tran_name,
                        tran_id: this.form.tran_id,
                    }
                    if(this.form.node_id==127){
                        Object.assign(params, payParams)
                    }else if(this.form.node_id==132){
                        Object.assign(params, perfectParams)
                    }
                    API.apiLicencesReapplyExecWork(params).then(res => {
                        if(res.code == 0){
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
            },
            handelOutstanding(){
                let num = 0
                if(this.form.some_collect_cast.choose.length<=0){
                    this.form.some_collect_cast.together_cost = this.form.some_collect_cast.this_cost
                }else{
                    this.form.no_pay.forEach(item2=>{
                        this.form.some_collect_cast.choose.forEach(item=>{
                            if(item2.id==item){
                                num+=Number(item2.money)
                            }
                        })
                    })
                    this.form.some_collect_cast.together_cost = num+Number(this.form.some_collect_cast.this_cost)
                }
            },
            handleUploadSuccess_pay_img(res, file, fileList){
                this.form.pay_info.pay_img = res.data.url
            },
            handleUploadSuccess_cost_img(res, file, fileList){
                this.form.some_collect_cast.cost_img = res.data.url
            },
            handleUploadSuccess_spot_money_img(res, file, fileList){
                this.form.spot_money_img = res.data.url
            },
            handleUploadSuccess_splicense_img(res, file, fileList){
                this.form.splicense_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_sup_img(res, file, fileList){
                this.form.sup_img = res.data.url
            },
            handleUploadSuccess(res, file, fileList){
                this.drive_license_img = res.data.url
            },
            handleBeforeUpload(){

            }
        }
    }
</script>

